import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import shuju from '@/components/shuju'
import pubuliu from '@/components/pubuliu'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/shuju',
      name: 'shuju',
      component: shuju
    },
    {
      path: '/pubuliu',
      name: 'pubuliu',
      component: pubuliu
    },
  ]
})



