<template>
  <div id="nav">
    <div class="nav">
      <p class="navtext">vue</p>
      <a href="javascript:;" :class="{'navicon':isA,'close':!isA}" @click="showCont"><span></span><span></span><span></span></a>
      <ul class="nav_list" v-if="show">
        <li @click="navsub"><router-link to="/">首页</router-link></li>
        <li @click="navsub"><router-link to="shuju">数据绑定</router-link></li>
        <li @click="navsub"><router-link to="pubuliu">瀑布流</router-link></li>
      </ul>
      <div class="mask" v-if="show"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: '',
  data () {
    return {
      show:false,
      isA: true
    }
  },
  methods: {
    showCont:function(){
      this.show = !this.show;
      this.hidden = !this.hidden;
      this.isA = !this.isA;
    },
    navsub:function(){
      this.show = !this.show;
      this.hidden = !this.hidden;
      this.isA = !this.isA;
    },
  },
}
</script>

<style scoped>


</style>



