<template>
  <div class="shuju">
    <h1>{{ msg }}</h1>
    <img :src="img">
    <ul class="imglist">
      <li v-for="img in imglist" class="imglistsub"><img v-bind:src="img.src"><p class="text" >{{img.name}}</p></li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'shuju',
  data () {
    return {
      msg: '数据绑定',
      img:require('../../static/images/1.jpg'), 
      imglist:[
        {
          src:require('../../static/images/2.jpg'), 
          name:"甄姬"
        },
        {
          src:require('../../static/images/3.jpg'), 
          name:"张良"
        },
        {
          src:require('../../static/images/4.jpg'), 
          name:"貂蝉"
        },
        {
          src:require('../../static/images/5.jpg'), 
          name:"安其拉"
        },
      ]
    }
  }
}
</script>

<style scoped>
img{width:100%;}
.imglist{width:100%;}
.imglistsub{display:block;width:50%;float:left;margin:0;}
.imglistsub img{display:block;width:90%;margin:10px auto;}
</style>



